import { type Actions } from '../actions/shared';

export type ShowVideoLoaderMessage = { action: Actions.ShowPageLoader };

export type DirectVideoOpenPayload = {
  watchUrl: string;
  time: string | null;
  videoId: string;
};

export type DirectVideoOpenMessage = {
  action: Actions.DirectVideoOpen;
  payload: DirectVideoOpenPayload;
};

export type FailedVideoOpenMessage = {
  action: Actions.FailedVideoOpen;
  payload: {
    watchUrl: string;
  };
};

export type VideoOpenerReadyMessage = {
  action: Actions.VideoOpenerReady;
};

export type NetworkConnectionRetryMessage = {
  action: Actions.NetworkConnectionRetry;
};

export type NetworkConnectionRetrySuccessMessage = {
  action: Actions.NetworkConnectionRetrySuccess;
};

export type NetworkConnectionRetryFailMessage = {
  action: Actions.NetworkConnectionRetryFail;
};

export type OpenPopupMessage = {
  action: Actions.OpenPopup;
};

export type OpenPopupPaymentMessage = {
  action: Actions.OpenPopupPayment;
};

export type NavigateToFreeTrialMessage = {
  action: Actions.NavigateToFreeTrial;
};

export type NavigateToPaymentMessage = {
  action: Actions.NavigateToPayment;
  payload?: {
    isSaleActive: boolean;
    isTrial: boolean;
    isGuestMode: boolean;
  };
};

export type PopupReadyMessage = {
  action: Actions.PopupReady;
};

export type SaveTimeMessage = {
  action: Actions.SaveTime;
  payload: {
    time: number;
    videoId: string;
  };
};

export type OpenPopupChangeDeviceMessage = {
  action: Actions.OpenPopupChangeDevice;
};

export type CreateNewUserMessage = {
  action: Actions.CreateNewUser;
  payload?: {
    isSaleActive: boolean;
    isGuestMode: boolean;
    isTrial: boolean;
  };
};

export type CreateNewUserSuccessMessage = {
  action: Actions.CreateNewUserSuccess;
  payload?: {
    isSaleActive: boolean;
    isGuestMode: boolean;
    isTrial: boolean;
  };
};

export type Message =
  | ShowVideoLoaderMessage
  | DirectVideoOpenMessage
  | FailedVideoOpenMessage
  | VideoOpenerReadyMessage
  | NetworkConnectionRetryMessage
  | NetworkConnectionRetrySuccessMessage
  | NetworkConnectionRetryFailMessage
  | OpenPopupMessage
  | NavigateToFreeTrialMessage
  | PopupReadyMessage
  | OpenPopupPaymentMessage
  | NavigateToPaymentMessage
  | SaveTimeMessage
  | OpenPopupChangeDeviceMessage
  | CreateNewUserMessage
  | CreateNewUserSuccessMessage;

export function isMessageOfType<T extends Message['action']>(
  msg: unknown,
  action: T,
): msg is Extract<Message, { action: T }> {
  return (
    typeof msg === 'object' &&
    msg !== null &&
    'action' in msg &&
    (msg as any).action === action
  );
}
